import React from 'react';
import {
  View,
  StyleSheet,
  ScrollView,
  Alert,
} from 'react-native';
import {
  Text,
  Card,
  Button,
  Divider,
  List,
  IconButton,
} from 'react-native-paper';
import { useSelector, useDispatch } from 'react-redux';
import { logoutUser } from '../store/slices/authSlice';

const ProfileScreen = ({ navigation }) => {
  const dispatch = useDispatch();
  const { user } = useSelector((state) => state.auth);

  const handleLogout = () => {
    Alert.alert(
      '',
      '',
      [
        { text: '', style: 'cancel' },
        { 
          text: '', 
          style: 'destructive',
          onPress: () => {
            dispatch(logoutUser());
            navigation.replace('Login');
          }
        }
      ]
    );
  };

  const getPhaseDisplayName = (phase) => {
    const phaseMap = {
      'pre_surgery': '',
      'post_surgery_early': '',
      'phase_3_1': '',
      'phase_3_2': '',
      'phase_3_3': '',
      'phase_3_4': '',
      'completed': '',
    };
    return phaseMap[phase] || '';
  };

  const formatDate = (dateString) => {
    if (!dateString) return '';
    return new Date(dateString).toLocaleDateString('ja-JP');
  };

  const menuItems = [
    {
      title: 'ACL-RSI',
      description: '',
      icon: 'clipboard-check',
      onPress: () => Alert.alert('', ''),
    },
    {
      title: '',
      description: '',
      icon: 'bell',
      onPress: () => Alert.alert('', ''),
    },
    {
      title: '',
      description: '',
      icon: 'download',
      onPress: () => Alert.alert('', ''),
    },
    {
      title: '',
      description: '',
      icon: 'help-circle',
      onPress: () => Alert.alert('', ''),
    },
  ];

  return (
    <ScrollView style={styles.container}>
      {/*  */}
      <Card style={styles.profileCard}>
        <Card.Content>
          <View style={styles.profileHeader}>
            <View style={styles.avatar}>
              <Text style={styles.avatarText}>
                {user?.name?.charAt(0) || 'P'}
              </Text>
            </View>
            <View style={styles.profileInfo}>
              <Text style={styles.patientName}>{user?.name || ''}</Text>
              <Text style={styles.patientNumber}>
                : {user?.patientNumber || 'N/A'}
              </Text>
              <Text style={styles.currentPhase}>
                : {getPhaseDisplayName(user?.currentPhase)}
              </Text>
            </View>
          </View>
        </Card.Content>
      </Card>

      {/*  */}
      <Card style={styles.infoCard}>
        <Card.Content>
          <Text style={styles.sectionTitle}></Text>
          
          <View style={styles.infoRow}>
            <Text style={styles.infoLabel}></Text>
            <Text style={styles.infoValue}>
              {formatDate(user?.dateOfBirth)}
            </Text>
          </View>
          
          <Divider style={styles.infoDivider} />
          
          <View style={styles.infoRow}>
            <Text style={styles.infoLabel}></Text>
            <Text style={styles.infoValue}>
              {user?.gender === 'male' ? '' : user?.gender === 'female' ? '' : ''}
            </Text>
          </View>
          
          <Divider style={styles.infoDivider} />
          
          <View style={styles.infoRow}>
            <Text style={styles.infoLabel}></Text>
            <Text style={styles.infoValue}>
              {formatDate(user?.surgeryDate)}
            </Text>
          </View>
          
          <Divider style={styles.infoDivider} />
          
          <View style={styles.infoRow}>
            <Text style={styles.infoLabel}></Text>
            <Text style={styles.infoValue}>
              {user?.email || ''}
            </Text>
          </View>
        </Card.Content>
      </Card>

      {/*  */}
      <Card style={styles.menuCard}>
        <Card.Content>
          <Text style={styles.sectionTitle}></Text>
          
          {menuItems.map((item, index) => (
            <List.Item
              key={index}
              title={item.title}
              description={item.description}
              left={(props) => (
                <List.Icon
                  {...props}
                  icon={item.icon}
                  color="#666"
                />
              )}
              right={(props) => (
                <List.Icon
                  {...props}
                  icon="chevron-right"
                  color="#999"
                />
              )}
              onPress={item.onPress}
              style={styles.menuItem}
            />
          ))}
        </Card.Content>
      </Card>

      {/*  */}
      <Card style={styles.appInfoCard}>
        <Card.Content>
          <Text style={styles.sectionTitle}></Text>
          
          <View style={styles.infoRow}>
            <Text style={styles.infoLabel}></Text>
            <Text style={styles.infoValue}>1.0.0</Text>
          </View>
          
          <Divider style={styles.infoDivider} />
          
          <View style={styles.infoRow}>
            <Text style={styles.infoLabel}></Text>
            <Text style={styles.infoValue}>202412</Text>
          </View>
          
          <Divider style={styles.infoDivider} />
          
          <Text style={styles.disclaimer}>
            
            
          </Text>
        </Card.Content>
      </Card>

      {/*  */}
      <View style={styles.logoutContainer}>
        <Button
          mode="outlined"
          onPress={handleLogout}
          style={styles.logoutButton}
          textColor="#F44336"
          icon="logout"
        >
          
        </Button>
      </View>
    </ScrollView>
  );
};

const styles = StyleSheet.create({
  container: {
    flex: 1,
    backgroundColor: '#f5f5f5',
  },
  profileCard: {
    margin: 16,
    marginBottom: 8,
    elevation: 2,
  },
  profileHeader: {
    flexDirection: 'row',
    alignItems: 'center',
  },
  avatar: {
    width: 64,
    height: 64,
    borderRadius: 32,
    backgroundColor: '#2E7D32',
    justifyContent: 'center',
    alignItems: 'center',
    marginRight: 16,
  },
  avatarText: {
    fontSize: 24,
    fontWeight: 'bold',
    color: 'white',
  },
  profileInfo: {
    flex: 1,
  },
  patientName: {
    fontSize: 20,
    fontWeight: 'bold',
    marginBottom: 4,
  },
  patientNumber: {
    fontSize: 14,
    color: '#666',
    marginBottom: 2,
  },
  currentPhase: {
    fontSize: 14,
    color: '#2E7D32',
    fontWeight: '500',
  },
  infoCard: {
    margin: 16,
    marginTop: 8,
    marginBottom: 8,
    elevation: 2,
  },
  sectionTitle: {
    fontSize: 16,
    fontWeight: 'bold',
    marginBottom: 12,
  },
  infoRow: {
    flexDirection: 'row',
    justifyContent: 'space-between',
    alignItems: 'center',
    paddingVertical: 8,
  },
  infoLabel: {
    fontSize: 14,
    color: '#333',
    flex: 1,
  },
  infoValue: {
    fontSize: 14,
    color: '#666',
    flex: 1,
    textAlign: 'right',
  },
  infoDivider: {
    marginVertical: 4,
  },
  menuCard: {
    margin: 16,
    marginTop: 8,
    marginBottom: 8,
    elevation: 2,
  },
  menuItem: {
    paddingVertical: 4,
  },
  appInfoCard: {
    margin: 16,
    marginTop: 8,
    marginBottom: 8,
    elevation: 1,
    backgroundColor: '#FAFAFA',
  },
  disclaimer: {
    fontSize: 12,
    color: '#666',
    lineHeight: 18,
    marginTop: 8,
    fontStyle: 'italic',
  },
  logoutContainer: {
    padding: 16,
    paddingTop: 8,
  },
  logoutButton: {
    borderColor: '#F44336',
    borderWidth: 1,
  },
});

export default ProfileScreen;