// : 
export const fetchAppointments = createAsyncThunk(
  'medicalCollaboration/fetchAppointments',
  async ({ patientId, token }, { rejectWithValue }) => {
    try {
      const response = await medicalCollaborationAPI.getAppointments(patientId, token);
      return response;
    } catch (error) {
      return rejectWithValue(error.response?.data?.error || '');
    }
  }
);

const initialState = {
  // 
  medicalStaff: {
    primaryDoctor: null,
    physiotherapist: null,
    nurses: [],
    specialists: [],
  },
  
  // 
  messages: [],
  unreadCount: 0,
  activeConversation: null,
  
  // 
  sharedReports: [],
  pendingReports: [],
  reportTemplates: [],
  
  // 
  appointments: [],
  nextAppointment: null,
  
  // 
  notifications: [],
  alerts: [],
  
  // 
  sharingPreferences: {
    autoShareProgress: true,
    autoShareACLRSI: true,
    autoShareAIAnalysis: false,
    weeklyReportEnabled: true,
    emergencyContactEnabled: true,
  },
  
  notificationSettings: {
    newMessage: true,
    appointmentReminder: true,
    reportRequested: true,
    urgentAlert: true,
    weeklyDigest: false,
  },
  
  // UI
  isLoading: false,
  error: null,
  lastUpdated: null,
  activeTab: 'team', // team, reports, settings
};

const medicalCollaborationSlice = createSlice({
  name: 'medicalCollaboration',
  initialState,
  reducers: {
    // 
    addMessage: (state, action) => {
      const message = {
        id: Date.now().toString(),
        timestamp: new Date().toISOString(),
        isRead: true,
        isSent: true,
        ...action.payload,
      };
      
      state.messages.unshift(message);
      
      // 100
      if (state.messages.length > 100) {
        state.messages = state.messages.slice(0, 100);
      }
    },
    
    markMessageAsRead: (state, action) => {
      const messageId = action.payload;
      const message = state.messages.find(m => m.id === messageId);
      if (message && !message.isRead) {
        message.isRead = true;
        state.unreadCount = Math.max(0, state.unreadCount - 1);
      }
    },
    
    markAllMessagesAsRead: (state) => {
      state.messages.forEach(message => {
        message.isRead = true;
      });
      state.unreadCount = 0;
    },
    
    setActiveConversation: (state, action) => {
      state.activeConversation = action.payload;
    },
    
    // 
    addPendingReport: (state, action) => {
      const report = {
        id: Date.now().toString(),
        createdAt: new Date().toISOString(),
        status: 'pending',
        ...action.payload,
      };
      
      state.pendingReports.unshift(report);
    },
    
    updateReportStatus: (state, action) => {
      const { reportId, status } = action.payload;
      
      // pending reports
      const pendingIndex = state.pendingReports.findIndex(r => r.id === reportId);
      if (pendingIndex !== -1) {
        if (status === 'sent') {
          // pending  shared 
          const report = state.pendingReports[pendingIndex];
          report.status = 'sent';
          report.sentAt = new Date().toISOString();
          state.sharedReports.unshift(report);
          state.pendingReports.splice(pendingIndex, 1);
        } else {
          state.pendingReports[pendingIndex].status = status;
        }
      }
    },
    
    // 
    addNotification: (state, action) => {
      const notification = {
        id: Date.now().toString(),
        timestamp: new Date().toISOString(),
        isRead: false,
        ...action.payload,
      };
      
      state.notifications.unshift(notification);
      
      // unreadCount
      if (notification.type === 'message') {
        state.unreadCount += 1;
      }
      
      // 50
      if (state.notifications.length > 50) {
        state.notifications = state.notifications.slice(0, 50);
      }
    },
    
    markNotificationAsRead: (state, action) => {
      const notificationId = action.payload;
      const notification = state.notifications.find(n => n.id === notificationId);
      if (notification && !notification.isRead) {
        notification.isRead = true;
      }
    },
    
    // 
    addAlert: (state, action) => {
      const alert = {
        id: Date.now().toString(),
        timestamp: new Date().toISOString(),
        isActive: true,
        ...action.payload,
      };
      
      state.alerts.unshift(alert);
      
      // 10
      if (state.alerts.length > 10) {
        state.alerts = state.alerts.slice(0, 10);
      }
    },
    
    dismissAlert: (state, action) => {
      const alertId = action.payload;
      const alert = state.alerts.find(a => a.id === alertId);
      if (alert) {
        alert.isActive = false;
        alert.dismissedAt = new Date().toISOString();
      }
    },
    
    // 
    updateSharingPreferences: (state, action) => {
      state.sharingPreferences = {
        ...state.sharingPreferences,
        ...action.payload,
      };
    },
    
    updateNotificationSettings: (state, action) => {
      state.notificationSettings = {
        ...state.notificationSettings,
        ...action.payload,
      };
    },
    
    // UI
    setActiveTab: (state, action) => {
      state.activeTab = action.payload;
    },
    
    clearError: (state) => {
      state.error = null;
    },
    
    // 
    setDummyData: (state) => {
      const now = new Date();
      
      // 
      state.medicalStaff = {
        primaryDoctor: {
          id: 'doc_001',
          name: ' ',
          title: '',
          specialization: '',
          hospital: '',
          email: 'tanaka@sports-med.jp',
          phone: '03-1234-5678',
          avatar: null,
          isOnline: true,
          lastSeen: new Date().toISOString(),
        },
        physiotherapist: {
          id: 'pt_001',
          name: ' ',
          title: '',
          specialization: '',
          hospital: '',
          email: 'sato@sports-med.jp',
          phone: '03-1234-5679',
          avatar: null,
          isOnline: false,
          lastSeen: new Date(now.getTime() - 30 * 60 * 1000).toISOString(),
        },
        nurses: [
          {
            id: 'nurse_001',
            name: ' ',
            title: '',
            specialization: '',
            hospital: '',
            email: 'yamada@sports-med.jp',
            avatar: null,
            isOnline: true,
          }
        ],
        specialists: [
          {
            id: 'spec_001',
            name: ' ',
            title: '',
            specialization: '',
            hospital: '',
            email: 'suzuki@sports-med.jp',
            avatar: null,
            isOnline: false,
          }
        ],
      };
      
      // 
      state.messages = [
        {
          id: '1',
          senderId: 'pt_001',
          senderName: ' ',
          senderTitle: '',
          content: '',
          timestamp: new Date(now.getTime() - 2 * 60 * 60 * 1000).toISOString(),
          type: 'text',
          isRead: false,
          isSent: false,
        },
        {
          id: '2',
          senderId: 'patient',
          senderName: '',
          content: '2/10103',
          timestamp: new Date(now.getTime() - 90 * 60 * 1000).toISOString(),
          type: 'text',
          isRead: true,
          isSent: true,
        },
        {
          id: '3',
          senderId: 'doc_001',
          senderName: ' ',
          senderTitle: '',
          content: 'ACL-RSI',
          timestamp: new Date(now.getTime() - 24 * 60 * 60 * 1000).toISOString(),
          type: 'text',
          isRead: true,
          isSent: false,
        },
      ];
      
      state.unreadCount = 1;
      
      // 
      state.sharedReports = [
        {
          id: 'report_001',
          title: '',
          type: 'weekly_progress',
          createdAt: new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000).toISOString(),
          sentAt: new Date(now.getTime() - 7 * 24 * 60 * 60 * 1000 + 60000).toISOString(),
          status: 'sent',
          recipients: ['doc_001', 'pt_001'],
          data: {
            sessionsCompleted: 5,
            totalExercises: 42,
            averagePainLevel: 2.1,
            averageBorgScale: 11.8,
            noteableProgress: 'ROM'
          }
        },
        {
          id: 'report_002',
          title: 'ACL-RSI',
          type: 'acl_rsi',
          createdAt: new Date(now.getTime() - 3 * 24 * 60 * 60 * 1000).toISOString(),
          sentAt: new Date(now.getTime() - 3 * 24 * 60 * 60 * 1000 + 120000).toISOString(),
          status: 'sent',
          recipients: ['doc_001', 'spec_001'],
          data: {
            totalScore: 72,
            emotionScore: 68,
            confidenceScore: 75,
            interpretation: '',
            recommendations: ['', '']
          }
        }
      ];
      
      // 
      state.appointments = [
        {
          id: 'apt_001',
          title: '',
          doctor: state.medicalStaff.primaryDoctor,
          datetime: new Date(now.getTime() + 3 * 24 * 60 * 60 * 1000).toISOString(),
          duration: 30,
          location: ' 3F A',
          type: 'consultation',
          status: 'confirmed',
          notes: '',
        },
        {
          id: 'apt_002',
          title: '',
          doctor: state.medicalStaff.physiotherapist,
          datetime: new Date(now.getTime() + 5 * 24 * 60 * 60 * 1000).toISOString(),
          duration: 60,
          location: ' 2F ',
          type: 'physiotherapy',
          status: 'confirmed',
          notes: '',
        }
      ];
      
      state.nextAppointment = state.appointments[0];
      
      // 
      state.notifications = [
        {
          id: 'notif_001',
          type: 'message',
          title: '',
          content: '',
          timestamp: new Date(now.getTime() - 2 * 60 * 60 * 1000).toISOString(),
          isRead: false,
          sender: state.medicalStaff.physiotherapist,
        },
        {
          id: 'notif_002',
          type: 'appointment_reminder',
          title: '',
          content: '3',
          timestamp: new Date(now.getTime() - 6 * 60 * 60 * 1000).toISOString(),
          isRead: true,
          appointment: state.appointments[0],
        },
        {
          id: 'notif_003',
          type: 'report_shared',
          title: '',
          content: 'ACL-RSI',
          timestamp: new Date(now.getTime() - 3 * 24 * 60 * 60 * 1000).toISOString(),
          isRead: true,
          report: state.sharedReports[1],
        }
      ];
      
      state.lastUpdated = new Date().toISOString();
    },
  },
  
  extraReducers: (builder) => {
    builder
      // 
      .addCase(fetchMedicalStaff.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(fetchMedicalStaff.fulfilled, (state, action) => {
        state.isLoading = false;
        state.medicalStaff = action.payload.medicalStaff;
        state.lastUpdated = new Date().toISOString();
      })
      .addCase(fetchMedicalStaff.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;
      })
      
      // 
      .addCase(sendReport.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(sendReport.fulfilled, (state, action) => {
        state.isLoading = false;
        // pending report
        const reportId = action.payload.reportId;
        const pendingIndex = state.pendingReports.findIndex(r => r.id === reportId);
        if (pendingIndex !== -1) {
          const report = state.pendingReports[pendingIndex];
          report.status = 'sent';
          report.sentAt = new Date().toISOString();
          state.sharedReports.unshift(report);
          state.pendingReports.splice(pendingIndex, 1);
        }
      })
      .addCase(sendReport.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;
      })
      
      // 
      .addCase(fetchMessages.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(fetchMessages.fulfilled, (state, action) => {
        state.isLoading = false;
        state.messages = action.payload.messages;
        state.unreadCount = action.payload.unreadCount;
        state.lastUpdated = new Date().toISOString();
      })
      .addCase(fetchMessages.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;
      })
      
      // 
      .addCase(sendMessage.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(sendMessage.fulfilled, (state, action) => {
        state.isLoading = false;
        // 
        const message = {
          ...action.payload.message,
          isSent: true,
          isRead: true,
        };
        state.messages.unshift(message);
      })
      .addCase(sendMessage.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;
      })
      
      // 
      .addCase(fetchAppointments.pending, (state) => {
        state.isLoading = true;
        state.error = null;
      })
      .addCase(fetchAppointments.fulfilled, (state, action) => {
        state.isLoading = false;
        state.appointments = action.payload.appointments;
        state.nextAppointment = action.payload.nextAppointment;
        state.lastUpdated = new Date().toISOString();
      })
      .addCase(fetchAppointments.rejected, (state, action) => {
        state.isLoading = false;
        state.error = action.payload;
      });
  },
});

export const {
  addMessage,
  markMessageAsRead,
  markAllMessagesAsRead,
  setActiveConversation,
  addPendingReport,
  updateReportStatus,
  addNotification,
  markNotificationAsRead,
  addAlert,
  dismissAlert,
  updateSharingPreferences,
  updateNotificationSettings,
  setActiveTab,
  clearError,
  setDummyData,
} = medicalCollaborationSlice.actions;

export default medicalCollaborationSlice.reducer;